import baseIncludes from 'lodash/includes';
import some from 'lodash/some';

import { isRegExp } from './isRegExp2';

export const includes = (collection, values) => {
  // // eslint-disable-next-line no-param-reassign
  // if (!Array.isArray(collection)) collection = [collection];
  // eslint-disable-next-line no-param-reassign
  if (!Array.isArray(values)) values = [values];
  // console.log({ collection, values });
  return some(values, (value) => {
    if (isRegExp(value)) {
      // console.log({ value }, isRegExp(value));
      const array = Array.isArray(collection) ? collection : [collection];
      return some(array, (elem) => value.test(elem));
    }
    // console.log('baseIncludes', { collection, value });
    return baseIncludes(collection, value);
  });
};
export default includes;
