// временная проверка на нужные нам сайты, чтобы не ломать все остальные сайты
if (
  ['youtube.com', 'twitch.tv', 'instagram.com', 'tiktok.com'].some((hostname) =>
    document.location.hostname.includes(hostname),
  )
) {
  // небольшой костыль для того, чтобы загружаться до всего, но прогрузить body для react-modal
  if (!document.querySelector('body')) {
    const body = document.createElement('body');
    document.querySelector('html').appendChild(body);
  }
  require('./app');
}
