/* eslint-disable no-param-reassign */
/* global document window */

import config from '../config';
import { addXMLRequestCallback } from './addXMLRequestCallback';
import { collectPayload } from './collectPayload';
import { includes } from './includes';
import { log } from './log';

const { baseURL } = config.client;
export function addInterceptor({ urls, xhrUrls, provider, endpoint } = {}) {
  const location = document?.location?.href || '';
  if (!!urls && !includes(location, urls)) return { status: 'ignore urls' };
  return addXMLRequestCallback(async (xhr) => {
    const { url } = xhr.meta;
    if (!!xhrUrls && !includes(url, xhrUrls)) return { status: 'ignore xhrUrls' };
    const payload = {
      provider,
      endpoint,
      ...collectPayload(xhr),
    };
    log.trace(`${baseURL}/instagram_userscript`, payload); // eslint-disable-line no-console
    window.__lskjs.GM_xmlhttpRequest({
      method: 'POST',
      url: `${baseURL}/instagram_userscript`,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      data: JSON.stringify(payload),
    });
    return { status: 'success' };
  });
}

export default addInterceptor;
