import BuzzGuru from '@buzzguru/ui/assets/icons/buzzguru-logo-big';
import Avatar from '@buzzguru/ui/Avatar';
import FlagIcon from '@buzzguru/ui/FlagIcon';
// import Quality from '@buzzguru/ui/kit/Quality';
// import Button from '@lskjs/button';
// import Tooltip from '@buzzguru/ui/Tooltip';
import Link from '@lskjs/link';
import truncate from 'lodash/truncate';
// import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React from 'react';

import { FlagWrapper, globalStyles, LanguageWrapper, Quality, Title, Wrapper } from './LeftContent.styles';
import Relations from './Relations';

globalStyles();

const LeftContent = ({ entity }) => {
  const { item } = entity;
  const general = item?.generalScore || 0;
  return (
    <Wrapper>
      <Link href={'https://buzzguru.com/home'}>
        <BuzzGuru width="144px" style={{ marginRight: 22 }} />
      </Link>
      <Avatar
        title={item?.name}
        src={item?.avatar}
        defaultAvatar="/assets/images/blogger.png"
        size={36}
        style={{ marginRight: 12 }}
      />
      <Title>{truncate(item?.name, { length: 20 })}</Title>
      {/* <Button
        style={{ width: 24, height: 24, borderRadius: 4, backgroundColor: '#4A4A4A80', marginRight: 12 }}
        view="empty"
        isRipple={false}
        icon={<FlagIcon code={item?.channelInfo?.country || ''} style={{ width: 16, height: 12 }} />}
      /> */}
      {item?.country ? (
        <FlagWrapper>
          <FlagIcon code={item?.country || ''} style={{ width: 16, height: 12 }} />
        </FlagWrapper>
      ) : (
        ''
      )}
      {item?.language ? <LanguageWrapper>{String(item?.language).toUpperCase()}</LanguageWrapper> : ''}
      <Relations entity={entity} />
      <Quality percent={general} />
    </Wrapper>
  );
};

LeftContent.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
};

export default LeftContent;
