/* global window */
import Button from '@lskjs/button';
import Link from '@lskjs/link';
import T from '@lskjs/t';
import PropTypes from 'prop-types';
import React from 'react';

const LoginPage = ({ app }) => {
  const login = () => {
    window?.__lskjs?.GM_setValue('auth', 'authToken');
    console.log('authToken', window?.__lskjs?.GM_getValue('auth'));
    app.redirect('/');
  };
  return (
    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '100%' }}>
      <Button paint="common" block style={{ width: '100%', margin: 12 }} onClick={login}>
        <T name="buttons.login" />
      </Button>
    </div>
  );
};

LoginPage.propTypes = {
  app: PropTypes.objectOf(Object).isRequired,
};

export default LoginPage;
