import BuzzGuruLogo from '@buzzguru/ui/assets/icons/ext-buzzguru';
import styled from '@emotion/styled';
import ButtonBase from '@lskjs/button';
import PropTypes from 'prop-types';
import React from 'react';

export const Button = styled(ButtonBase)`
  @keyframes buttonAnimation {
    0% {
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    25% {
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
      -webkit-transform: rotate(20deg);
      transform: rotate(20deg);
    }
    50% {
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
      -webkit-transform: rotate(40deg);
      transform: rotate(40deg);
    }
    75% {
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
      -webkit-transform: rotate(20deg);
      transform: rotate(20deg);
    }
    100% {
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
  }
  &:hover {
    animation: buttonAnimation 0.25s;
  }
`;

const Trigger = ({ onClick }) => (
  <Button view="empty" icon={<BuzzGuruLogo size={64} />} onClick={onClick} isRipple={false} />
);

Trigger.propTypes = {
  onClick: PropTypes.func.isRequired,
};

export default Trigger;
