/* eslint-disable no-undef */
/* global document */
import merge from 'lodash/merge';

import configClient from './config/client';
import ReactApp from './ReactApp.client';
import Uapp from './Uapp';

const rootStateConfig = window.__ROOT_STATE__ ? window.__ROOT_STATE__.config : {};

const config = merge({}, configClient, rootStateConfig);

export const toolbar = async () => {
  const node = document.createElement('div');

  const app = await ReactApp.create({
    container: node,
    debug: 1,
    config,
    modules: {
      uapp: () => Uapp,
    },
  });

  global.app = app;
  global.uapp = app.uapp;
  app.start();
  if (!document.body.getAttribute('tabindex')) document.body.appendChild(node);

  return app;
};

export default toolbar;
