/* global document  */

export function collectPayload(xhr) {
  const responseHeaders = {};
  const responseHeadersArray = xhr
    .getAllResponseHeaders()
    .split('\n')
    .filter((str) => str);
  responseHeadersArray.forEach((header) => {
    const [key, value] = header.split(/: (.+)/);
    responseHeaders[key] = value;
  });

  const { url = '' } = xhr.meta;
  const origin = document?.location?.origin || '';
  // if (!isAbsoluteUrl(url)) url = origin + url;
  const referer = document?.location?.href || '';

  return {
    request: {
      origin,
      url,
      method: xhr.meta.method,
      headers: xhr.requestHeaders,
      body: xhr.meta.body,
      referer,
    },
    response: {
      headers: responseHeaders,
      data: xhr.response.data,
    },
  };
}

export default collectPayload;
