import Button from '@lskjs/button';
import Link from '@lskjs/link';
import React, { useContext } from 'react';

import { ToolbarContext } from '../../Toolbar';
import Header from '../Header';
import LongPanel from '../LongPanel';
import { Card, Content, PopupWrapper } from './Popup.styles';

const Popup = ({ children, entity, app }) => {
  const ctx = useContext(ToolbarContext);
  const { view } = ctx;
  const { item, loading } = entity || {};

  const action = !loading && item?.link && (
    <Button
      componentClass={Link}
      paint="primary"
      size="small"
      isRipple={false}
      target="_blank"
      href={`https://buzzguru.com${item.link}`}
    >
      Go to channel
    </Button>
  );

  if (view === 'long') {
    return <LongPanel entity={entity} app={app} />;
  }
  return (
    <PopupWrapper>
      <Card flex shadow={false} style={{ borderRadius: 12 }}>
        <Content>{children}</Content>
      </Card>
    </PopupWrapper>
  );
};

export default Popup;
