export default async function storeFetcher(store, cb = async () => {}) {
  // TODO: Костыль для await после SSR
  if (__SERVER__ || (!__DEV__ && __CLIENT__ && window.routerCount === 1)) {
    if (store.item && !store.loading) return null;
    if (store.items && store.items.length > 0 && !store.loading) return null;
    await store.fetch();
    await cb(store);
  } else if (__CLIENT__ && (window.routerCount > 1 || __DEV__)) {
    store.fetch().then(() => cb(store));
  }
  return null;
}
