import ExtErrorIcon from '@buzzguru/ui/assets/icons/ext-error';
import ExtWarningIcon from '@buzzguru/ui/assets/icons/ext-warning';
import Typography from '@buzzguru/ui/Typography2';
import T from '@lskjs/t';
import React from 'react';

import Wrapper, { IconWrapper } from './Alert.styles';

const Alert = ({ status = 'loading', ...props }) => {
  let title;
  let icon;
  if (status === 'loading') {
    title = <T name="extention.alert.warning" />;
    icon = <ExtWarningIcon size={24} />;
  }
  if (status === 'error') {
    title = <T name="extention.alert.error" />;
    icon = <ExtErrorIcon size={24} />;
  }
  return (
    <Wrapper {...props}>
      <IconWrapper>{icon}</IconWrapper>
      <Typography view="extra-1" color="#fff" lineHeight="24px" letterSpacing="-0.01em">
        {title}
      </Typography>
    </Wrapper>
  );
};

export default Alert;
