import BuzzGuru from '@buzzguru/ui/assets/icons/buzzguru-logo-big';
import FlagIcon from '@buzzguru/ui/FlagIcon';
import Link from '@lskjs/link';
import PropTypes from 'prop-types';
import React from 'react';

import Mention from '../BottomContent/Mention';
import Stats from '../BottomContent/Stats';
import Contacts from '../TopContent/LeftContent/Relations';
import RightContent from '../TopContent/RightContent';
import Wrapper, { FlagWrapper, LanguageWrapper, LeftContent, Quality } from './LongPanel.styles';

const LongPanel = ({ entity, app, ...props }) => {
  const { item } = entity;
  const general = item?.generalScore || 0;
  return (
    <Wrapper {...props}>
      <LeftContent>
        <Link href={'https://buzzguru.com/home'} style={{ marginRight: 22 }}>
          <BuzzGuru width="144px" />
        </Link>
        {item?.country ? (
          <FlagWrapper>
            <FlagIcon code={item?.country || ''} style={{ width: 16, height: 12 }} />
          </FlagWrapper>
        ) : (
          ''
        )}
        {item?.language ? <LanguageWrapper>{String(item?.language).toUpperCase()}</LanguageWrapper> : ''}
        <Contacts entity={entity} placement="TOP_LEFT" />
        <Quality percent={general} />
        <Stats entity={entity} />
        <Mention entity={entity} />
      </LeftContent>
      <RightContent entity={entity} app={app} style={{ marginLeft: 12 }} />
    </Wrapper>
  );
};

LongPanel.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  app: PropTypes.objectOf(Object).isRequired,
};

export default LongPanel;
