/* global document window */
import qs from 'qs';


export default async () => {
  let pathName = document?.location?.href || '';
  const youtubeRegexp = /(https?:\/\/)?(www\.)?(youtube\.com|youtu\.be)\/[\/\wа-яА-Яё\-_\?=&]+/gim;
  const instagramRegexp = /(?:(?:http|https):\/\/)?(?:www.)?(?:instagram.com|instagr.am)\/([A-Za-z0-9-_]+)/gim;
  const tiktokRegexp = /tiktok.com\/@([a-zA-Z0-9_]+)/gim;
  const twitchRegexp = /^(?:http(?:s?):\/\/)?(?:w{0,3})\.?twitch\.tv\/([a-zA-Z0-9_]+)/gim;
  const twitchVideo = /twitch\.tv\/videos\/(\d+)/i;
  if (pathName.match(youtubeRegexp)) {
    if (pathName.includes('/watch?')) {
      const { v } = qs.parse(document.location.search.slice(1));
      if (!v) return null;
      return { type: 'youtubeVideo', id: v };
    }
    const isChannelHTML = !!document.querySelector('ytd-browse[role="main"] #channel-header');
    if (isChannelHTML) {
      const f = document.querySelector('ytd-browse[role="main"] form#form');
      if (f && f.action) {
        pathName = new URL(f.action).pathname;
      }
    }
    let url;
    if (pathName.includes('/search')) {
      url = pathName.replace(/\/search$/, '/about');
    } else {
      const pathAndId = /\/(c|channel|user)\/([^\/?#\s()]+)/.exec(decodeURIComponent(pathName));
      if (!pathAndId) return null;
      url = `/${pathAndId[1]}/${pathAndId[2]}/about`;
    }
    const channelResponse = await window.fetch(url).then((res) => res.text());
    const [, json2] = channelResponse.match(/(?<=var ytInitialData\s=\s)(.*?)(?=;<\/script>)/) || [];
    const ytInitialResponse = JSON.parse(json2);
    const metadata = ytInitialResponse.metadata.channelMetadataRenderer;
    const providerId = metadata.externalId;
    return { type: 'youtube', id: providerId, idType: 'providerId' };
  }
  if (pathName.match(instagramRegexp)) {
    const match = instagramRegexp.exec(pathName);
    if (match) {
      return { type: 'instagram', id: match[1] };
    }
  }
  if (pathName.match(tiktokRegexp)) {
    const match = tiktokRegexp.exec(pathName);
    return { type: 'tiktok', id: match[1] };
  }
  if (pathName.match(twitchRegexp)) {
    if (pathName.includes('/videos/')) {
      const match = twitchVideo.exec(pathName);
      const id = match?.[1];
      if (!id) return null;
      return { type: 'twitchStream', id };
    }
    const match = twitchRegexp.exec(pathName);
    return { type: 'twitch', id: match[1] };
  }
  return null;
};
