/* global XMLHttpRequest */

export function addXMLRequestCallback(callback) {
  let oldSend;
  let oldSetRequestHeader;
  let oldOpen;
  let i;
  if (XMLHttpRequest.callbacks) {
    XMLHttpRequest.callbacks.push(callback);
  } else {
    XMLHttpRequest.callbacks = [callback];
    oldSend = XMLHttpRequest.prototype.send;
    oldSetRequestHeader = XMLHttpRequest.prototype.setRequestHeader;
    oldOpen = XMLHttpRequest.prototype.open;
    XMLHttpRequest.prototype.send = function (body) {
      this.meta.body = body;
      oldSend.apply(this, arguments); // eslint-disable-line prefer-rest-params
    };
    XMLHttpRequest.prototype.open = function (method, url, async, user, password) {
      this.meta = {
        method,
        url,
        async,
        user,
        password,
      };
      this.addEventListener('load', function () {
        for (i = 0; i < XMLHttpRequest.callbacks.length; i += 1) {
          // eslint-disable-line no-plusplus
          XMLHttpRequest.callbacks[i](this);
        }
      });
      oldOpen.apply(this, arguments); // eslint-disable-line prefer-rest-params
    };
    XMLHttpRequest.prototype.setRequestHeader = function (key, value) {
      if (!this.requestHeaders) {
        this.requestHeaders = {};
      }
      this.requestHeaders[key] = value;
      oldSetRequestHeader.apply(this, arguments); // eslint-disable-line prefer-rest-params
    };
  }
}

export default addXMLRequestCallback;
