import ExtPriceIcon from '@buzzguru/ui/assets/icons/ext-cost';
import ExtErIcon from '@buzzguru/ui/assets/icons/ext-engagement';
import ExtCpmIcon from '@buzzguru/ui/assets/icons/ext-lightning';
import ExtLikesIcon from '@buzzguru/ui/assets/icons/ext-likes';
import ExtViewsIcon from '@buzzguru/ui/assets/icons/ext-views';
import Tooltip from '@buzzguru/ui/Tooltip';
import Typography from '@buzzguru/ui/Typography2';
import { toShort } from '@lskjs/utils/formatter';
import get from 'lodash/get';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React from 'react';

import beauty from '../../../utils/beauty';
import { Wrapper } from './Stats.styles';

const Stats = ({ entity, i18 }) => {
  const { item } = entity;

  let erMedianValue = get(item, 'er') ? get(item, 'er').toFixed(2) : 0;
  if (erMedianValue > 100) erMedianValue = 100;
  const erMedian = `${erMedianValue}%`;

  const cpmMin = get(item, 'cpmMin', 0) / 100;
  const cpmMax = get(item, 'cpmMax', 0) / 100;

  const stats = [
    {
      id: 'avgViews',
      value: toShort(Math.ceil(get(item, 'views', 0))),
      title: i18.t('channelHeaderInfo.averageViewsCount'),
      icon: <ExtViewsIcon color="#7070ff" size={24} />,
    },
    {
      id: 'avgLikes',
      value: toShort(Math.ceil(get(item, 'likes', 0))),
      title: i18.t('channelHeaderInfo.averageLikesCount'),
      icon: <ExtLikesIcon color="#7070ff" size={24} />,
    },
    {
      id: 'price',
      value: `$${beauty(get(item, 'priceMin', 0) / 100)} - $${beauty(get(item, 'priceMax', 0) / 100)}`,
      title: i18.t('channelHeaderInfo.intergrationCosts'),
      icon: <ExtPriceIcon color="#7070ff" size={24} />,
    },
    {
      id: 'er',
      value: erMedian,
      title: i18.t('er'),
      icon: <ExtErIcon color="#7070ff" size={24} />,
    },
    {
      id: 'cpm',
      value: `$${cpmMin}-${cpmMax}`,
      title: i18.t('channel.cost.cpm.footer'),
      icon: <ExtCpmIcon color="#7070ff" size={24} />,
    },
  ];
  return (
    <Wrapper id="stats">
      {stats.map((stat) => (
        <Tooltip key={stat.id} id={stat.id} overlay={stat.title}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              paddingRight: 24,
              flexDirection: 'row',
            }}
          >
            {stat.icon}
            <Typography view="extra-1" style={{ paddingLeft: 8 }} color="#ffffff">
              {stat.value}
            </Typography>
          </div>
        </Tooltip>
      ))}
    </Wrapper>
  );
};

Stats.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
};

export default inject('i18')(Stats);
