import DotsIcon from '@buzzguru/ui/assets/icons/dots-icon-horizontal';
import Typography from '@buzzguru/ui/Typography2';
import Button from '@lskjs/button';
import PopoverMenu from '@lskjs/ui/PopoverMenu';
import sortBy from 'lodash/sortBy';
import PropTypes from 'prop-types';
import React from 'react';

import iconMapping from './iconMapping';
import { DropdownItem } from './Relations.styles';

const colors = {
  vk: '#4266b2',
  instagram: '#F00075',
  telegram: '#0088cb',
  fb: '#4266b2',
  twitter: '#1996ef',
  twitch: '#863fff',
  tiktok: '#000000',
  pinterest: '#e20020',
  mail: 'primary',
};
function convertTypeToIcon(array) {
  const prepared = array
    .map((baseItem, index) => {
      const item = baseItem;
      if ((item?.url || '').includes('tiktok.com')) {
        item.type = 'tiktok';
      }
      const icon = iconMapping[item.type];
      const color = colors[item.type] || null;
      if (!icon && !item.title) return null;
      let href = `https://buzzguru.com/${item.type}/channels/${item._id}`;
      if (!item.type || !item._id) href = null;

      return {
        id: `${index}-${item.type}`,
        overlay: item.title,
        href,
        icon: icon || iconMapping.web,
        color,
        componentClass: 'a',
        target: '_blank',
        rel: 'noopener noreferrer nofollow',
      };
    })
    .filter((e) => !!e);
  return sortBy(prepared, 'icon').reverse();
}

const Relations = ({ entity, placement }) => {
  const { item } = entity;
  const links = convertTypeToIcon(item?.relations || []);
  return (
    <PopoverMenu
      id="extention_contacts_drop"
      isToggleHover={false}
      layer={links.map(({ overlay = 'Untitled', ...prps }) => (
        <DropdownItem {...prps} key={prps.id} style={{ textDecoration: 'none' }}>
          <Typography view="title-2" lineHeight="20px">
            {overlay}
          </Typography>
        </DropdownItem>
      ))}
      layerStyle={{
        width: 256,
        backgroundColor: '#313137',
        borderRadius: 8,
        padding: '8px 0',
        boxShadow: '0px 10px 30px rgba(0, 0, 0, 0.08)',
        zIndex: 9999,
      }}
      transitionDelay={300}
      closeOnOutsideClick
      trigger={({ triggerRef, toggle }) => (
        <Button
          icon={<DotsIcon size={12} color="#ffffff" />}
          view="empty"
          type="button"
          htmlRef={triggerRef}
          onClick={toggle}
          onMouseEnter={null}
          isRipple={false}
          style={{ width: 24, height: 24, borderRadius: 4, backgroundColor: '#4A4A4A80', marginRight: 12 }}
        />
      )}
      placement={{
        anchor: placement,
        autoAdjust: true,
        snapToAnchor: false,
        triggerOffset: 16,
      }}
    />
  );
};

Relations.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  placement: PropTypes.string,
};
Relations.defaultProps = {
  placement: 'TOP_RIGHT',
};

export default Relations;
