import { isDev } from '@lskjs/env';
import MobxStores from '@lskjs/mobx/mobxStores';
import BaseUappModule from '@lskjs/uapp';
import getLocale from '@lskjs/uapp/i18/getLocale';
import BaseUappProvider from '@lskjs/uapp/UappProvider';
import get from 'lodash/get';

import I18Client from './I18Client';
import routes from './routes';
import ScrollToModule from './ScrollToModule';
import stores from './stores';
import setLocale from './utils/setLocale';
import withExternals from './utils/withExternals';

const UappProvider = withExternals(BaseUappProvider);

class UappModule extends BaseUappModule {
  setLocale = setLocale; // TODO: переделать
  getLocale = getLocale; // TODO: переделать
  Provider = UappProvider; // TODO: переделать

  async init() {
    await super.init();
    this.__app = this.app;
    this.app = this;
  }

  getRoutes() {
    return {
      ...super.getRoutes(),
      ...routes,
    };
  }

  async getModules() {
    return {
      ...(await super.getModules()),
      i18: () => I18Client,
      stores: [() => MobxStores, { stores }],
      scrollTo: () => ScrollToModule,
    };
  }

  async initModules() {
    const i18 = await this.module('i18');
    const locale = this.getLocale() || get(i18, 'config.fallbackLng', 'en'); // TODO: переделать
    this.i18 = await i18.instance(locale, { anyLocale: true, throwError: false }); // TODO: переделать
    this.i18.locales = i18.config.locales; // TODO: переделать, взять из конфигов внутри модуля
  }

  async provide() {
    return {
      ...(await super.provide()),
      uapp: this,
      req: this.req,
      i18: this.i18,
      theme: require('@buzzguru/ui/assets/theme').default,
    };
  }

  async run() {
    await super.run();
    await this.initModules();
  }

  catchError(fn) {
    return async (...args) => {
      try {
        const res = await fn(...args);
        return res;
      } catch (err) {
        this.onError(err);
        throw err;
      }
    };
  }

  async resolveFinish() {
    this.req.path = this.__app.history.location.pathname;
    this.req.pathname = this.__app.history.location.pathname;
    this.req.query = this.__app.history.location.search;
  }

  async resolve(reqParams = {}) {
    this.emit('resolve:start', reqParams);
    const req = {
      ...this.req,
      path: reqParams.path,
      pathname: reqParams.path,
      query: reqParams.query,
    };
    try {
      if (this.isClient) this.log.debug('resolve', req.path, req.query);
      const page = await this.module('page');
      await page.beforeResolve();
      const providers = await this.__provide();
      const res = await this.router.resolve({
        pathname: this.__app.history.location.pathname,
        path: this.__app.history.location.pathname,
        query: this.__app.history.location.search,
        state: this.__app.history.location.state,
        // req,
        page,
        req,
        ...providers,
      });
      await page.afterResolve();
      this.emit('resolve:finish', { req, res, page });
      return { res, page };
    } catch (err) {
      this.log.error('uapp.router.resolve ERR:', err);
      this.emit('resolve:error', { req, err });
      throw err;
    }
  }

  // @autobind
  onError(err) {
    if ((err && err.type === 'cancel') || (err && err.code === 'page.cancel') || err === 'page.cancel') {
      return null;
    }
    return this.toast(err, { defaultType: 'error' });
  }

  toast(err) {
    this.log.error('[toast]', err);
  }

  redirect(...args) {
    if (isDev) this.log.trace('Uapp.redirect', ...args);
    const [link] = args;
    if ((link && link.startsWith('http://')) || link.startsWith('https://')) {
      window.location.href = link;
    } else {
      this.__app.redirect(...args);
    }
  }
}

export default UappModule;
