/* eslint-disable max-classes-per-file */
import I18Module from '@lskjs/i18/I18Module';
import BaseI18MobxInstance from '@lskjs/i18/instances/I18MobxInstance';
import i18next from 'i18next';

class I18MobxInstance extends BaseI18MobxInstance {
  async createInstance() {
    // eslint-disable-next-line no-async-promise-executor
    return new Promise(async (resolve, reject) => {
      const instance = i18next.createInstance();
      if (this.debug) this.applyLogger(instance);
      const { lng, locale, ...props } = this.config;
      // props.lng = lng || locale || 'en';
      props.lng = 'en'; // only en version

      // eslint-disable-next-line no-promise-executor-return
      return instance
        .init(props)
        .then(() => resolve(instance))
        .catch((err) => {
          this.log.error('init', err);
          return reject(err);
        });
    });
  }
}

class I18Client extends I18Module {
  I18Instance = I18MobxInstance;
}

export default I18Client;
