import { isDev as isLocalDev, stage } from '@lskjs/env';

export const isProd = () => stage === 'semrush' || stage === 'staging' || stage === 'production' || stage === 'prod';
export const isOnlyProd = () => stage === 'semrush' || stage === 'production' || stage === 'prod';
export const isDev = () => isLocalDev || !isProd();
export const isIsuvorov = () => stage === 'isuvorov';
export const isSemrush = () => stage === 'semrush';
export const getWorkerUrl = (url) => {
  if (isIsuvorov()) return `http://localhost:8881${url}`;
  if (isProd() || stage === 'testing') return `https://worker.buzz.guru${url}`;
  return `https://dev-worker.buzz.guru${url}`;
};

export default { isDev, isProd, getWorkerUrl };
