/* eslint-disable max-classes-per-file */
import CrudApi from '@lskjs/mobx/mobxStores/CrudApi';
import CrudStore from '@lskjs/mobx/mobxStores/CrudStore';
import EntityStore from '@lskjs/mobx/mobxStores/EntityStore';
import ListStore from '@lskjs/mobx/mobxStores/ListStore';
import capitalize from 'lodash/capitalize';
import get from 'lodash/get';
import set from 'lodash/set';
import { computed, observable } from 'mobx';

import cpm from '../utils/cpm';
import { isDev } from '../utils/stage';
import storeFetcher from '../utils/storeFetcher';

export class ChannelApi extends CrudApi {
  baseURL = `https://buzzguru.com/api/channels`;

  findOne(initParams) {
    let params = { _id: initParams };
    if (typeof initParams === 'object') {
      const { id, _id, ...oth } = initParams;
      params = { _id: _id || id, ...oth };
    }
    return this.fetch('/findOne', {
      method: 'POST',
      data: params,
    });
  }

  getChannelCount() {
    return this.fetch('/getChannelCount', {
      method: 'GET',
    });
  }

  getViewsDynamics({ _id, type = 'year' }) {
    return this.fetch('/viewsDynamics', {
      method: 'POST',
      data: { _id, type },
    });
  }
  getChannelRelations({ _id, type }) {
    return this.fetch('/relations', {
      method: 'POST',
      data: { _id, type },
    });
  }
  getViewsDynamics2({ _id, type = 'day', value = 30, delts = false }) {
    return this.fetch('/viewsDynamics', {
      method: 'POST',
      data: { _id, type, value, delts },
    });
  }
  getSubscribersDynamics({ _id, type = 'year', channelType = 'youtube' }) {
    return this.fetch('/subscribersDynamics', {
      method: 'POST',
      data: { _id, type, channelType },
    });
  }
  getSubscribersDynamics2({ _id, type = 'day', value = 30, delts = false }) {
    return this.fetch('/subscribersDynamics', {
      method: 'POST',
      data: { _id, type, value, delts },
    });
  }
  getTwitchDynamics({ _id, type = 'year', queryType = 'followers' }) {
    return this.fetch('/twitchDynamics', {
      method: 'POST',
      data: { _id, type, queryType },
    });
  }
  getStreamsDynamics({ _id, from, to }) {
    return this.fetch('/streamsDynamics', {
      method: 'POST',
      data: { _id, from, to },
    });
  }
  getProductMentions({ _id, type = 'app', channelType = 'youtube', limit = 8 }) {
    return this.fetch('/productMentions', {
      method: 'POST',
      data: { _id, type, limit, channelType },
    });
  }
  getForecastViews({ _id }) {
    return this.fetch('/forecastViews', {
      method: 'POST',
      data: { _id },
    });
  }
  getGamesRatings({ _id, type }) {
    return this.fetch('/gamesRatings', {
      method: 'POST',
      data: { _id, type },
    });
  }
  getPublicationTime({ _id }) {
    return this.fetch('/publicationTime', {
      method: 'POST',
      data: { _id },
    });
  }
  getMetrics({ _id }) {
    return this.fetch('/metrics', {
      method: 'POST',
      data: {
        v2: true,
        channelId: _id,
        filter: {
          status: 'public',
        },
        sort: [
          {
            name: 'time',
            order: 'ASC',
          },
        ],
      },
    });
  }
  getStreamsMetrics({ channelId }) {
    return this.fetch('/streamsMetrics', {
      method: 'POST',
      data: {
        channelId,
      },
    });
  }
  getRatings({ _id }) {
    return this.fetch('/ratings', {
      method: 'POST',
      data: { _id },
    });
  }
}

class ChannelStore extends CrudStore {
  static Api = ChannelApi;

  @observable _id;
  @observable info;
  @observable channelInfo;
  @observable type = 'youtube';

  @observable.deep viewsDynamics;
  @observable.deep viewsDynamics2;
  @observable.deep subscribersDynamics;
  @observable.deep subscribersDynamics2;
  @observable.deep dynamics;
  @observable.deep streamsDynamics;
  @observable.deep ratings;
  @observable.deep productMentions;
  @observable.deep gamesRatings;
  @observable.deep publicationTime;
  @observable.deep forecastViews;
  @observable.deep metrics;
  @observable.deep streamsMetrics;
  @observable.deep channelRelations;

  static async getChannelCount() {
    return this.wrap(this.api.getChannelCount());
  }

  getWrapStore(type) {
    let storeName = capitalize(type);
    if (['site', 'app', 'game'].includes(type)) storeName = 'Product';
    return this.constructor.app.stores[`${storeName}Store`];
  }

  wrapProductMention(item, type) {
    if (!['app', 'game', 'site'].includes(type)) return item;
    return {
      ...item,
      product: new (this.getWrapStore(type))(item.product),
    };
  }

  normalizeMethod(method) {
    const catcher = async (data) => {
      let res = {};
      try {
        res = await this.constructor.api[method](data);
      } catch (error) {
        if (isDev()) console.log(error);
      }
      return res;
    };

    return async (data, params) => this.constructor.wrap(catcher(data), params);
  }

  createEntityStore(method, params = {}) {
    return new EntityStore({
      Api: {
        find: method,
        findOne: method,
      },
      api: {
        find: method,
        findOne: method,
      },
      getFindParams: () => params,
    });
  }

  async fetchFieldComposer({ method: _method, field, params, fetch = true }) {
    const method = this.normalizeMethod(_method);
    const entityInstance = this.createEntityStore(method, params);
    set(this, field, entityInstance);
    if (fetch) {
      await storeFetcher(entityInstance);
    }
    return entityInstance;
  }

  async fetchViewsDynamics(type = 'year') {
    return this.fetchFieldComposer({
      method: 'getViewsDynamics',
      field: `viewsDynamics.${type}`,
      params: {
        _id: this._id,
        type,
      },
    });
  }

  async fetchChannelRelations() {
    return this.fetchFieldComposer({
      method: 'getChannelRelations',
      field: 'channelRelations',
      params: {
        _id: this._id,
        type: this.type,
      },
    });
  }

  async fetchViewsDynamics2({ type = 'day', value = 30, delts = false }) {
    return this.fetchFieldComposer({
      method: 'getViewsDynamics2',
      field: `viewsDynamics2`,
      params: {
        _id: this._id,
        type,
        value,
        delts,
      },
    });
  }

  async fetchSubscribersDynamics(type = 'year', channelType = 'youtube') {
    return this.fetchFieldComposer({
      method: 'getSubscribersDynamics',
      field: `subscribersDynamics.${type}`,
      params: {
        _id: this._id,
        type,
        channelType,
      },
    });
  }

  async fetchSubscribersDynamics2({ type = 'day', value = 30, delts = false }) {
    return this.fetchFieldComposer({
      method: 'getSubscribersDynamics2',
      field: `subscribersDynamics2`,
      params: {
        _id: this._id,
        type,
        value,
        delts,
      },
    });
  }

  async fetchTwitchDynamics(type = 'year', queryType = 'followers') {
    return this.fetchFieldComposer({
      method: 'getTwitchDynamics',
      field: `dynamics.${queryType}.${type}`,
      params: {
        _id: this._id,
        type,
        queryType,
      },
    });
  }

  async fetchStreamsDynamics(from, to) {
    return this.fetchFieldComposer({
      method: 'getStreamsDynamics',
      field: 'streamsDynamics',
      params: {
        _id: this._id,
        from,
        to,
      },
    });
  }

  async fetchRatings() {
    return this.fetchFieldComposer({
      method: 'getRatings',
      field: 'ratings',
      params: {
        _id: this._id,
      },
    });
  }

  async fetchProductMentions(type = 'app', channelType = 'youtube', limit = 8, ignoreHidden = false) {
    const entity = await this.fetchFieldComposer({
      method: 'getProductMentions',
      field: `productMentions.${type}`,
      params: {
        _id: this._id,
        type,
        limit,
        channelType,
        ignoreHidden,
      },
    });
    set(
      entity,
      'item.items',
      get(entity, 'item.items', []).map((item) => this.wrapProductMention(item, type)),
    );

    return entity;
  }

  async fetchGamesRatings(type) {
    const entity = await this.fetchFieldComposer({
      method: 'getGamesRatings',
      field: 'gamesRatings',
      params: {
        _id: this._id,
        type,
      },
    });

    const wrapGame = (item) => this.wrapProductMention(item, 'game');
    set(entity, 'item.productsMedians14day', get(entity, 'item.productsMedians14day', []).map(wrapGame));
    set(entity, 'item.productsMedians30day', get(entity, 'item.productsMedians30day', []).map(wrapGame));
    set(entity, 'item.productsMedians60day', get(entity, 'item.productsMedians60day', []).map(wrapGame));
    set(entity, 'item.productsMedians90day', get(entity, 'item.productsMedians90day', []).map(wrapGame));
    set(entity, 'item.productsMedians180day', get(entity, 'item.productsMedians180day', []).map(wrapGame));
    return entity;
  }

  async fetchPublicationTime() {
    return this.fetchFieldComposer({
      method: 'getPublicationTime',
      field: 'publicationTime',
      params: {
        _id: this._id,
      },
    });
  }

  async fetchForecastViews() {
    return this.fetchFieldComposer({
      method: 'getForecastViews',
      field: 'forecastViews',
      params: {
        _id: this._id,
      },
    });
  }

  async fetchChannelMetrics() {
    return this.fetchFieldComposer({
      method: 'getMetrics',
      field: 'metrics',
      params: {
        _id: this._id,
      },
    });
  }
  async fetchChannelStreamsMetrics() {
    return this.fetchFieldComposer({
      method: 'getStreamsMetrics',
      field: 'streamsMetrics',
      params: {
        channelId: this._id,
      },
    });
  }

  async fetchRelatedChannels(params) {
    const relatedChannelsListStore = new ListStore({
      api: ChannelStore,
      limit: 20,
      ...params,
    });
    this.relatedChannelsListStore = relatedChannelsListStore;

    if (get(this, 'channelInfo.relatedChannels')) {
      await relatedChannelsListStore.fetch();
    }
    return relatedChannelsListStore;
  }

  async fetchFeaturedChannels(params) {
    const featuredChannelsListStore = new ListStore({
      api: ChannelStore,
      limit: 20,
      ...params,
    });
    this.featuredChannelsListStore = featuredChannelsListStore;

    if (get(this, 'channelInfo.featuredChannels')) {
      await featuredChannelsListStore.fetch();
    }
    return featuredChannelsListStore;
  }

  @computed
  get id() {
    return `${this.prefixId}${this._id}`;
  }

  @computed
  get prefixId() {
    if (this.type === 'youtube') return 'yt_';
    if (this.type === 'twitch') return 'tw_';
    return '';
  }

  @computed
  get externalLink() {
    if (!['youtube', 'twitch'].includes(this.type)) return null;
    if (!this.providerId) return null;
    if (this.type === 'youtube') return `https://www.youtube.com/channel/${this.providerId}`;
    if (this.type === 'twitch') return `https://www.twitch.tv/${get(this.channelInfo, 'username')}`;
    return null;
  }

  @computed
  get link() {
    if (!['youtube', 'twitch'].includes(this.type)) return null;
    if (this.type === 'youtube') return `/youtube/channels/${this._id}`;
    if (this.type === 'twitch') return `/twitch/channels/${this._id}`;
    return null;
  }

  @computed
  get adCostFrom() {
    const adCost = get(this, 'average.adCost');
    if (typeof adCost !== 'number') return undefined;
    const adCostStd = get(this, 'average.adCostStd');
    if (typeof adCostStd !== 'number') return undefined;
    const adCostFrom = adCost - adCostStd;
    if (adCostFrom < 0) return 0;
    return adCostFrom;
  }

  @computed
  get adCostTo() {
    const adCost = get(this, 'average.adCost');
    if (typeof adCost !== 'number') return undefined;
    const adCostStd = get(this, 'average.adCostStd');
    if (typeof adCostStd !== 'number') return undefined;
    const adCostTo = adCost + adCostStd;
    if (adCostTo < 0) return 0;
    return adCostTo;
  }

  @computed
  get avgPriceMin() {
    const priceMin = get(this, 'average.avgPriceMin', 0);
    if (priceMin) return priceMin;
    const mx = get(this, 'average.avgPriceMx', 0);
    const dx = get(this, 'average.avgPriceDx', 0);
    return mx - dx;
  }

  @computed
  get avgPriceMax() {
    const priceMax = get(this, 'average.avgPriceMax', 0);
    if (priceMax) return priceMax;
    const mx = get(this, 'average.avgPriceMx', 0);
    const dx = get(this, 'average.avgPriceDx', 0);
    return mx + dx;
  }

  getCpmFromKey(key) {
    const country = get(this, 'channelInfo.country');
    return get(cpm, `countries.${country}.${key}`, get(cpm, `default.${key}`));
  }

  @computed
  get cpmMin() {
    return this.getCpmFromKey('cpm_from');
  }

  @computed
  get cpmMax() {
    return this.getCpmFromKey('cpm_to');
  }

  @computed
  get cpmRegion() {
    return (this.getCpmFromKey('cpm_from') + this.getCpmFromKey('cpm_to')) / 2;
  }

  @computed
  get avatars() {
    if (!['youtube', 'twitch'].includes(this.type)) return {};
    const avatar = get(this, 'channelInfo.avatar');
    if (!avatar) return {};
    if (this.type === 'youtube') {
      return {
        300: (avatar || '').replace('=s900', '=s300'),
        50: (avatar || '').replace('=s900', '=s50'),
      };
    }
    if (this.type === 'twitch') {
      return {
        300: avatar || '',
        50: (avatar || '').replace('300x300', '50x50'),
      };
    }
    return {};
  }

  @computed
  get poster() {
    if (!['youtube'].includes(this.type)) return {};
    const poster = get(this, 'channelInfo.banner');
    if (!poster) return {};
    if (this.type === 'youtube') {
      return {
        1168: (poster || '').replace('=w2120', '=w1168'),
        704: (poster || '').replace('=w2120', '=w704'),
      };
    }
    return {};
  }
}

export default ChannelStore;
