import Avatar from '@buzzguru/ui/Avatar';
import Tooltip from '@buzzguru/ui/Tooltip';
import Link from '@lskjs/link';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';

import getRequestParams from '../../../utils/getRequestParams';
import { MoreWrapper, Wrapper } from './Mention.styles';

const Mention = ({ entity, i18 }) => {
  const { item } = entity;
  const [type, setType] = useState('youtube');
  const appMentions = item?.mentionsApps || {};
  const gameMentions = item?.mentionsGames || {};
  // const siteMentions = item?.mentionsSites || {};
  useEffect(() => {
    getRequestParams().then((res) => setType(res.type || 'youtube'));
  }, []);
  return (
    <Wrapper>
      {(appMentions.items || []).length ? (
        <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}/products`}>
          <Tooltip id="apps-mentions" overlay={i18.t('productMentions.mentionOfProduct')}>
            {(appMentions?.items || [])
              .filter(({ product }) => (product.info?.image || '').length > 0)
              .slice(0, 3)
              .map(({ product }) => (
                <Avatar
                  id={product?.id}
                  key={product?.id}
                  size={36}
                  src={product?.info?.image}
                  defaultAvatar="https://buzzguru.com/assets/images/product.png"
                  shape="square"
                  innerStyle={{
                    borderRadius: 8,
                    border: '2px solid #313137',
                  }}
                  style={{
                    marginRight: -12,
                    background: 'none',
                  }}
                />
              ))}
            <MoreWrapper>{`+${appMentions?.totals?.totalUniqProductsCount || 0}`}</MoreWrapper>
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
      {/* <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}/sites`}>
        <Tooltip id="sites-mentions" overlay={i18.t('productMentions.mentionOfSite')}>
          {(siteMentions?.items || []).slice(0, 3).map(({ product }) => (
            <Avatar
              id={product?.id}
              key={product?.id}
              size={36}
              src={product?.info?.image}
              defaultAvatar="https://buzzguru.com/assets/images/product.png"
              shape="square"
              innerStyle={{
                borderRadius: 8,
                border: '2px solid #313137',
              }}
              style={{
                marginRight: -12,
                background: 'none',
              }}
            />
          ))}
          <MoreWrapper>{`+${siteMentions?.totals?.totalUniqProductsCount || 0}`}</MoreWrapper>
        </Tooltip>
      </Link> */}
      {(gameMentions.items || []).length ? (
        <Link target="_blank" href={`https://buzzguru.com/${type}/channels/${item?._id}/games`}>
          <Tooltip id="games-mentions" overlay={i18.t('gamesMentions.mentionOfGame')}>
            {(gameMentions?.items || []).slice(0, 3).map(({ product }) => (
              <Avatar
                id={product?.id}
                key={product?.id}
                size={36}
                src={product?.info?.image}
                defaultAvatar="https://buzzguru.com/assets/images/product.png"
                shape="square"
                innerStyle={{
                  borderRadius: 8,
                  border: '2px solid #313137',
                }}
                style={{
                  marginRight: -12,
                  background: 'none',
                }}
              />
            ))}
            <MoreWrapper>{`+${gameMentions?.totals?.totalUniqProductsCount || 0}`}</MoreWrapper>
          </Tooltip>
        </Link>
      ) : (
        ''
      )}
    </Wrapper>
  );
};

Mention.propTypes = {
  entity: PropTypes.objectOf(Object).isRequired,
  i18: PropTypes.objectOf(Object).isRequired,
};

export default inject('i18')(Mention);
